#Record{
  -webkit-animation: recorder 1s infinite linear;
          animation: recorder 1s infinite linear;
  transform-origin: center;
  transform-box: fill-box;
}

@-webkit-keyframes recorder{
  from{
    transform: rotateZ(0deg);
  }
  to{
    transform: rotateZ(360deg);
  }
}

@keyframes recorder{
  from{
    transform: rotateZ(0deg);
  }
  to{
    transform: rotateZ(360deg);
  }
}


#HeadPhones{
  -webkit-animation: hphones 1s infinite linear alternate;
          animation: hphones 1s infinite linear alternate;
  transform-origin: center;
  transform-box: fill-box;
}

@-webkit-keyframes hphones{
  from{
    transform: scale(1.0);
    -webkit-transform: scale(1.0);

  }
  to{
    transform: scale(1.1);
    -webkit-transform: scale(1.1);
  }
}

@keyframes hphones{
  from{
    transform: scale(1.0);
    -webkit-transform: scale(1.0);

  }
  to{
    transform: scale(1.1);
    -webkit-transform: scale(1.1);
  }
}

