#background {
  position: absolute;
  height: 100%;
  width: 100%;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-filter: blur(10px);
          filter: blur(10px);
  z-index: -1;
}

/* container for music player */
.container {
  position: absolute;
  height: 430pt;
  width: 320pt;
  margin-top: 370px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  display: flex;
  justify-content: center; 
  align-items: center;
  background: rgb(70, 67, 67);
  border-radius: 0px 0px 10px 10px;
  box-shadow: 0 0 3px #000000;
}

/* container for play, pause, previous track, next track, and progress bar  */
.box {
  position: absolute;
  height: 40%;
  width: 110%;
  background: -webkit-linear-gradient(
    45deg,
    rgb(63, 124, 63),
    rgb(133, 133, 133)
  );
  z-index: 4;
  bottom: -17%;
}

/* album cover */
#thumbnail {
  position: absolute;
  width: 103%;
  top: 0%;
  transition: 1s;
  z-index: 3;
}

/* track artist */
.song-artist {
  position: absolute;
  z-index: 4;
  color: rgb(193, 255, 6);
  font-family: "Saira ExtraCondensed", sans-serif;
  font-weight: bold;
  font-size: 20px;
  bottom: 0%;
  left: 10%;
}

/* track title */
.song-title {
  position: absolute;
  z-index: 4;
  color: white;
  font-family: "Saira ExtraCondensed", sans-serif;
  font-size: 12px;
  bottom: 7%;
  left: 10%;
}

/* style for play and pause images */
#play-pause {
  position: absolute;
  height: 50px;
  width: 50px;
  -webkit-filter: invert(1);
          filter: invert(1);
  cursor: pointer;
  bottom: 14%;
  z-index: 4;
  font-size: 53px;
  left: 10%;
}

/* style for next and previous song images */
#next-song,
#previous-song {
  position: absolute;
  height: 30px;
  width: 30px;
  -webkit-filter: invert(1);
          filter: invert(1);
  cursor: pointer;
  bottom: 5.5%;
  font-size: 40px;
  z-index: 4;
}

/* unique style for next song image */
#next-song {
  right: 10%;
}

/* unique style for previous song image */
#previous-song {
  right: 10.3%;
  bottom: 14.5%;
}

/* style for current time of the track */
.currentTime {
  position: absolute;
  height: 50px;
  width: 50px;
  cursor: pointer;
  bottom: -7.5%;
  left: 17%;
  z-index: 4;
  font-family: "Saira ExtraCondensed", sans-serif;
  font-size: 13px;
  color: white;
  align-items: center;
}

/* style for the total length of the track */
.durationTime {
  position: absolute;
  height: 50px;
  width: 50px;
  cursor: pointer;
  bottom: -1%;
  right: 11%;
  z-index: 4;
  font-family: "Saira ExtraCondensed", sans-serif;
  font-size: 13px;
  align-items: center;
  color: rgb(193, 255, 6);
}

/* style for the progress bar */
#progress-bar {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  position: absolute;
  z-index: 4;
  height: 5px;
  background: #000000;
  width: 160pt;
  bottom: 5%;
  border-radius: 30px;
  left: 17%;
}

/* style for the progress bar's slider thumb */
#progress-bar::-webkit-slider-thumb {
  -webkit-appearance: none;
  margin-top: -3px;
  margin-left: 0px;
  height: 10px;
  width: 7px;
  border: 0;
  background: -webkit-linear-gradient(
    45deg,
    rgb(213, 213, 213),
    rgb(193, 255, 6)
  );
  border-radius: 30px;
  cursor: pointer;
}

/* style for the progress bar's runnable track */
#progress-bar::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
  max-height: 5px;
  border-radius: 30px;
}

/* style to remove blue shade of the progress bar when clicked on/selected */
#progress-bar:focus {
  outline: none;
}

